import React, { useEffect, useState } from 'react';
import { makeStyles, Avatar, AppBar, Toolbar, InputBase, Typography } from '@material-ui/core';
import IconButton from '@material-ui/core/IconButton';
import SearchIcon from '@material-ui/icons/Search';
import HomeIcon from '@material-ui/icons/Home';
import { Link } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import { setSearchData, setSearchFlag, setEmptyData, setSearchValue } from '../../store/actions/ListPageActionTypes'
import axios from 'axios'
import logo from '../../assets/quick_trailer_logo.PNG'
import SpeechRecognition, { useSpeechRecognition } from 'react-speech-recognition';
import MicIcon from '@material-ui/icons/Mic';



const useStyles = makeStyles((theme) => ({
    grow: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {

        [theme.breakpoints.up('sm')]: {
            display: 'block',
            fontWeight: 'bold'
        },
    },
    search: {
        position: 'relative',
        borderRadius: theme.shape.borderRadius,
        backgroundColor: '#DFDFDF',
        '&:hover': {
            backgroundColor: '#DFDFDF',
        },
        marginRight: theme.spacing(0),
        marginLeft: 0,
        width: '100%',
        [theme.breakpoints.up('sm')]: {
            marginLeft: theme.spacing(4),
            width: 'auto',
        },
    },
    searchIcon: {
        padding: theme.spacing(0, 2),
        color: '#9b9b9b',
        height: '100%',
        position: 'absolute',
        pointerEvents: 'none',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },
    inputRoot: {
        color: 'inherit',
        [theme.breakpoints.down('md')]: {
           width:'190px',
        },
    },
    inputInput: {
        padding: theme.spacing(1, 1, 1, 0),
        // vertical padding + font size from searchIcon
        paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('md')]: {
            width: '60ch',
        },
    },
    sectionDesktop: {
        display: 'none',
        [theme.breakpoints.up('md')]: {
            display: 'flex',
        },
    },
    sectionMobile: {
        display: 'flex',
        [theme.breakpoints.up('md')]: {
            display: 'none',
        },
    },
    homeIcon: {
        color: '#9b9b9b'
    },
    app_logo: {
        width: 65,
        height: 65,
    },
    mic_icon_on: {
        color: '#3fde3f',
    },
    mic_icon_off: {
        color: '#9b9b9b',
    },
    icon_mic: {
        
        [theme.breakpoints.down('md')]: {
            position: 'absolute',
            right: '-6px',
            bottom: '-7px',

        },
    }
}));

const Header = () => {

    //speech recognition
    const {
        transcript,
        listening,
        resetTranscript,
        browserSupportsSpeechRecognition
    } = useSpeechRecognition();


    const classes = useStyles();
    const dispatch = useDispatch()
    const [searchText, setSearchText] = useState('')

    useEffect(() => {
        transcript &&
            setSearchText(transcript)
        dispatch(setSearchValue(transcript))
        axios.get(`https://api.themoviedb.org/3/search/movie?api_key=88428b2a9e9d271ea540df7c3fa4dac3&query=${transcript}`)
            .then(function (response) {
                // handle success
                if (response.data.results.length === 0) {
                    dispatch(setEmptyData())
                }
                else {
                    dispatch(setSearchData(response.data))
                    dispatch(setSearchValue(transcript))
                }

            })
            .catch(function (error) {
                // handle error
                console.log(error);
            })

    }, [transcript])


    const handleSearch = (e) => {
        console.log(e.target.value)
        if (e.target.value === '') {
            dispatch(setSearchFlag())
            setSearchText('')
        }
        else {
            dispatch(setSearchValue(e.target.value))
            setSearchText(e.target.value)
            axios.get(`https://api.themoviedb.org/3/search/movie?api_key=88428b2a9e9d271ea540df7c3fa4dac3&query=${e.target.value}`)
                .then(function (response) {
                    // handle success
                    if (response.data.results.length === 0) {
                        dispatch(setEmptyData())
                    }
                    else {
                        dispatch(setSearchData(response.data))
                    }

                })
                .catch(function (error) {
                    // handle error
                    console.log(error);
                })
        }
    }
    const onRedirect = () => {
        dispatch(setSearchFlag())
        setSearchText('')
    }


    const handleMicOn = () => {
        SpeechRecognition.startListening()
        setInterval(() => {
            SpeechRecognition.stopListening()
            // resetTranscript()
        }, 10000);
    }

    return (
        <div className={classes.grow}>
            <AppBar position="static" color="transparent" >
                <Toolbar>
                    {/* <Avatar alt="Profile Picture" className={classes.app_logo} src="data:image/png;base64," /> */}
                   <Link to="/"> <Avatar alt="Profile Picture" className={classes.app_logo} src={logo} /></Link>
                    {window.location.pathname.split("/")[1] === "detailpage" ? (
                        <Typography variant="h6" className={classes.title} type="h6">
                            Movie Details
                        </Typography>
                    ) : (
                        <div className={classes.search}>
                            <div className={classes.searchIcon}>
                                <SearchIcon />
                            </div>
                            <InputBase
                                placeholder="Search…"
                                value={searchText}
                                classes={{
                                    root: classes.inputRoot,
                                    input: classes.inputInput,
                                }}
                                inputProps={{ 'aria-label': 'search' }}
                                onChange={handleSearch}
                            />
                            <IconButton aria-label="Home" color="inherit" onClick={handleMicOn} className={classes.icon_mic}>
                                <MicIcon className={listening ? classes.mic_icon_on : classes.mic_icon_off} />
                            </IconButton>
                        </div>
                    )}
                    <div className={classes.grow} />
                    <div >
                        <IconButton aria-label="Home" color="inherit" className={classes.homeIcon} onClick={onRedirect}>
                            <Link to="/">
                                <HomeIcon />
                            </Link>
                        </IconButton>
                    </div>
                </Toolbar>
            </AppBar>
        </div>
    );
}

export default Header